:root {
  --primary-color: #F0F0F0;
  --secondary-color: #000;
  --tertiary-color: #333;

  --c0: #8a508f;
  --c1: #bc5090;
  --c2: #ff6361;
  --c3: #ff8531;
  --c4: #ffa600;
  --c5: #ffd380;

  --primary-text: white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: var(--primary-color);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--primary-text);
}

.App-link {
  color: var(--c4);
  text-decoration: none;
  font-style: italic;
}

.Emoji-placeholder {
  font-size: 100pt;
}

.Box-Glow {
  margin: 0;
  padding: 0;
  box-shadow: 0 0 25px 7px var(--tertiary-color);
  background: radial-gradient( circle 13cqi at center, var(--secondary-color), var(--tertiary-color) );
  border-radius: 10%;
}

.Text-Box {
  padding: 1em;
}
